:global .ant-drawer-header {
  color: white;
}
.close {
  color: #fff;
  position: fixed;
  width: 60px;
  line-height: 60px;
  left: -60px;
  top: 0;
  background-color: #1b1a20;
}
 .delete{
  color: #fff;
  font-weight: bold;
  font-size: 20px;
  position: absolute;
  right: 40px;
  top: 5px;
  width: 20px;
  cursor: pointer;
} 
 
 .deleteLine{
  text-decoration:line-through; 
}
 
.btn{
  background-color: #000;
  color: #b7b7b7;
  border: 0;
  width: 25px;
  height: 25px;
  text-align: center;
  line-height: 25px;
}
.line{
  border-top: 2px solid rgba(0,0,0,.2);
  width: 90%;
  margin: 0 auto;
 margin-top: -20px;
}
.element ::-webkit-scrollbar{
  display: none;
}
